import React from 'react';
import {useEffect, useReducer, useState} from 'react';
import {Link} from 'react-router-dom';
import './styling/search.css';
import './styling/new.css';
import './styling/err.css';
import './styling/filter.css';

var start = 0;
export var myData = [];

export const Search = () => {
    useEffect(() => {
        let myInput = document.getElementById('my-input');
        myInput.addEventListener('input', function(event) {
            if(event.keyCode === 13){
                let myBtn = document.getElementById('btn');
                myBtn.click();
            }
        });
    }, []);

    const [state, dispatch] = useReducer(reducer, {book: "", result: [], err: false});
    const [total, setTotal] = useState(0);
    const [num, setNum] = useState(1);
    const [search, setSearch] = useState("");
    const [load, setLoad] = useState(false);

    if(load) {
        document.body.style.overflowY = 'hidden';
    }else {
        document.body.style.overflowY = 'auto';
    }

    function reducer(state, action){
        switch (action.type){
            case "SET_BOOK":
                return {book: action.myBook, result: [], err: false};
            case "RESULT":
                myData = action.data;
                return {book: action.myBook, result: action.data, err: false};
            case "ERR":
                return {book: "", result: [], err: true};
            default :
                return {book: "", result: [], err: false};
        }
    }

    function handleChange (event) {
        const book = event.target.value;
        dispatch({type: "SET_BOOK", myBook: book});
        setSearch(book);
    }

    const myFetch = (url) => {
        setLoad(true);
        fetch(url)        
        .then(response => response.json())
        .then(data => {
            let final = [];
            let all = data.totalItems;
            final.push(...data.items);
            dispatch({type: "RESULT", data: final});
            setTotal(all);
            setLoad(false);
        }).catch(() => {setTotal(0); setLoad(false); return dispatch({type: "ERR"})});
    }

    function handleSubmit (event) {
        event.preventDefault();
        const url = `https://www.googleapis.com/books/v1/volumes?q=${state.book}&key=AIzaSyCSvxVOXpQ8DKk8rHHO2MGvv3BGYCnkphk&maxResults=30&printType=books`;
        myFetch(url);
        window.scrollTo(0, 500);
        setNum(1);
    }

    const next = () => {
        if(num < Math.ceil(total/30)){
            setNum(num + 1);
            start += 30;
            const url = `https://www.googleapis.com/books/v1/volumes?q=${search}&key=AIzaSyCSvxVOXpQ8DKk8rHHO2MGvv3BGYCnkphk&maxResults=30&startIndex=${start}&printType=books`;
            myFetch(url);
            window.scrollTo(0, 500);
        }
    }

    const pre = () => {
        if(num > 1){
            setNum(num - 1);
            start -= 30;
            const url = `https://www.googleapis.com/books/v1/volumes?q=${search}&key=AIzaSyCSvxVOXpQ8DKk8rHHO2MGvv3BGYCnkphk&maxResults=30&startIndex=${start}&printType=books`;
            myFetch(url);
            window.scrollTo(0, 500);
        }
    }

    return (<>
        <div className="search">
            <h1><span>المكتبة الإسلامية </span> أهلا بك في</h1>
            <div>
                <form onSubmit={handleSubmit} id="my-form">
                    <input onChange={handleChange} type="text" placeholder="إبحث عن كتاب أو قسم الكتاب أو إسم المؤلف" id="my-input" autoComplete="off"/>
                    <button id="btn" type="submit"></button>
                </form>
           </div>
        </div>
        <div className="new-list">
            <h2>نتائج البحث</h2>
            <div className="number-of-total">{total} : عدد نتائج البحث</div>
            <section id="books">
            {state.result.length !== 0 && 
            state.result.map((obj) => {
                return <Books key={obj.id}
                name={obj.volumeInfo.title || "لا يوجد إسم للكتاب"}
                author={obj.volumeInfo.authors && obj.volumeInfo.authors[0]}
                img={obj.volumeInfo.imageLinks && obj.volumeInfo.imageLinks.thumbnail}
                id={obj.id}/>
            })}
            {state.err && <Err/>}
            {load && <Loading/>}
            </section>
        </div>
        <div className="next-pre">
            <button id="pre" onClick={() => pre()}>&lt;</button>
            <span>{num}</span>
            <button id="next" onClick={() => next()}>&gt;</button>
        </div>
        </>
    )
}
const Err = () => {
    return <div className="err">
        <img src="data:image/svg+xml;base64," alt="404"/>
        <h2>لم يتم العثور علي الكتاب</h2>
    </div>
}

const Books = ({name, img, author, id}) => {
    return (
          <div>
            <img src={img} alt={name || "صورة الكتاب"}/>
            <h3>{name || "إسم الكتاب"}</h3>
            <h4>{author || "إسم المؤلف"}</h4>
            <Link to={`/${id}`}>
                <button onClick={() => {window.scroll(0, 0);}}>قراءة</button>
            </Link>
          </div>
    )
}

const Loading = () => {
    return <>
        <div className="loading">
            <span></span>
        </div>
    </>
}
